/* Basic */

@-ms-viewport {
    width: device-width;
}

@media screen and (max-width: 480px) {

    html, body {
        min-width: 320px;
    }

}

body {
    background: #fff;
}

.inner {
    margin: 0 auto;
    max-width: 65em;
}

    @media screen and (max-width: 1280px) {

        .inner {
            max-width: 90%;
        }

    }

    @media screen and (max-width: 980px) {

        .inner {
            max-width: 70%;
        }

    }

    @media screen and (max-width: 736px) {

        .inner {
            max-width: 85%;
        }

    }

/* Type */

body, input, select, textarea {
    color: #767d85;
    font-size: 13pt;
    font-weight: 300;
    line-height: 1.65;
}

    @media screen and (max-width: 1680px) {

        body, input, select, textarea {
            font-size: 11pt;
        }

    }

    @media screen and (max-width: 1280px) {

        body, input, select, textarea {
            font-size: 11pt;
        }

    }

    @media screen and (max-width: 980px) {

        body, input, select, textarea {
            font-size: 12pt;
        }

    }

    @media screen and (max-width: 736px) {

        body, input, select, textarea {
            font-size: 12pt;
        }

    }

    @media screen and (max-width: 480px) {

        body, input, select, textarea {
            font-size: 12pt;
        }

    }

p {
    margin: 0 0 2em 0;
}

h1 {
    color: #727a82;
    font-weight: 700;
    line-height: 1.5;
    margin: 0 0 1em 0;
}

    h1 a {
        color: inherit;
        text-decoration: none;
    }

h1 {
    font-size: 2em;
    line-height: 1.3;
}

/* Box */

.box {
    border-radius: 4px;
    border: solid 1px #dee1e3;
    margin-bottom: 2em;
    overflow: hidden;
}

    .box > :last-child,
    .box > :last-child > :last-child,
    .box > :last-child > :last-child > :last-child {
        margin-bottom: 0;
    }

    .box .image.fit {
        margin: 0;
    }

    .box .content {
        padding: 1.5em;
    }

    .box.alt {
        border: 0;
        border-radius: 0;
        padding: 0;
    }

/* Icon */

.icon {
    text-decoration: none;
    border-bottom: none;
    position: relative;
}

    .icon:before {
        -moz-osx-font-smoothing: grayscale;
        -webkit-font-smoothing: antialiased;
        font-family: FontAwesome;
        font-style: normal;
        font-weight: normal;
        text-transform: none !important;
    }

    .icon > .label {
        display: none;
    }

/* Image */

.image {
    border: 0;
    display: inline-block;
    position: relative;
}

    .image img {
        display: block;
    }

    .image.left, .image.right {
        max-width: 40%;
    }

        .image.left img, .image.right img {
            width: 100%;
        }

    .image.left {
        float: left;
        padding: 0 1.5em 1em 0;
        top: 0.25em;
    }

    .image.right {
        float: right;
        padding: 0 0 1em 1.5em;
        top: 0.25em;
    }

    .image.fit {
        display: block;
        margin: 0 0 2em 0;
        width: 100%;
    }

        .image.fit img {
            width: 100%;
        }

    .image.round img {
        border-radius: 100%;
    }

    .image.main {
        display: block;
        margin: 0 0 3em 0;
        width: 100%;
    }

        .image.main img {
            width: 100%;
        }

/* List */

ol {
    list-style: decimal;
    margin: 0 0 2em 0;
    padding-left: 1.25em;
}

    ol li {
        padding-left: 0.25em;
    }

ul {
    list-style: disc;
    margin: 0 0 2em 0;
    padding-left: 1em;
}

    ul li {
        padding-left: 0.5em;
    }

    ul.alt {
        list-style: none;
        padding-left: 0;
    }

        ul.alt li {
            border-top: solid 1px #dee1e3;
            padding: 0.5em 0;
        }

            ul.alt li:first-child {
                border-top: 0;
                padding-top: 0;
            }

    ul.icons {
        cursor: default;
        list-style: none;
        padding-left: 0;
    }

        ul.icons li {
            display: inline-block;
            padding: 0 1em 0 0;
        }

            ul.icons li:last-child {
                padding-right: 0;
            }

            ul.icons li .icon:before {
                font-size: 2em;
            }

    ul.actions {
        cursor: default;
        list-style: none;
        padding-left: 0;
    }

        ul.actions li {
            display: inline-block;
            padding: 0 1em 0 0;
            vertical-align: middle;
        }

            ul.actions li:last-child {
                padding-right: 0;
            }

        ul.actions.small li {
            padding: 0 0.5em 0 0;
        }

        ul.actions.vertical li {
            display: block;
            padding: 1em 0 0 0;
        }

            ul.actions.vertical li:first-child {
                padding-top: 0;
            }

            ul.actions.vertical li > * {
                margin-bottom: 0;
            }

        ul.actions.vertical.small li {
            padding: 0.5em 0 0 0;
        }

            ul.actions.vertical.small li:first-child {
                padding-top: 0;
            }

        ul.actions.fit {
            display: table;
            margin-left: -1em;
            padding: 0;
            table-layout: fixed;
            width: calc(100% + 1em);
        }

            ul.actions.fit li {
                display: table-cell;
                padding: 0 0 0 1em;
            }

                ul.actions.fit li > * {
                    margin-bottom: 0;
                }

            ul.actions.fit.small {
                margin-left: -0.5em;
                width: calc(100% + 0.5em);
            }

                ul.actions.fit.small li {
                    padding: 0 0 0 0.5em;
                }

        @media screen and (max-width: 480px) {

            ul.actions {
                margin: 0 0 2em 0;
            }

                ul.actions li {
                    padding: 1em 0 0 0;
                    display: block;
                    text-align: center;
                    width: 100%;
                }

                    ul.actions li:first-child {
                        padding-top: 0;
                    }

                    ul.actions li > * {
                        width: 100%;
                        margin: 0 !important;
                    }

                        ul.actions li > *.icon:before {
                            margin-left: -2em;
                        }

                ul.actions.small li {
                    padding: 0.5em 0 0 0;
                }

                    ul.actions.small li:first-child {
                        padding-top: 0;
                    }

        }

dl {
    margin: 0 0 2em 0;
}

    dl dt {
        display: block;
        font-weight: 700;
        margin: 0 0 1em 0;
    }

    dl dd {
        margin-left: 2em;
    }

/* Table */

.table-wrapper {
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
}

table {
    margin: 0 0 2em 0;
    width: 100%;
}

    table tbody tr {
        border: solid 1px #dee1e3;
        border-left: 0;
        border-right: 0;
    }

        table tbody tr:nth-child(2n + 1) {
            background-color: rgba(144, 144, 144, 0.075);
        }

    table td {
        padding: 0.75em 0.75em;
    }

    table th {
        color: #727a82;
        font-size: 0.9em;
        font-weight: 700;
        padding: 0 0.75em 0.75em 0.75em;
        text-align: left;
    }

    table thead {
        border-bottom: solid 2px #dee1e3;
    }

    table tfoot {
        border-top: solid 2px #dee1e3;
    }

    table.alt {
        border-collapse: separate;
    }

        table.alt tbody tr td {
            border: solid 1px #dee1e3;
            border-left-width: 0;
            border-top-width: 0;
        }

            table.alt tbody tr td:first-child {
                border-left-width: 1px;
            }

        table.alt tbody tr:first-child td {
            border-top-width: 1px;
        }

        table.alt thead {
            border-bottom: 0;
        }

        table.alt tfoot {
            border-top: 0;
        }

/* Flex */

.flex {
    display: -ms-flexbox;
    -ms-flex-wrap: nowrap;
    -ms-flex-pack: justify;
    -ms-flex-align: stretch;
    display: -moz-flex;
    display: -webkit-flex;
    display: -ms-flex;
    display: flex;
    -moz-flex-wrap: nowrap;
    -webkit-flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    flex-wrap: nowrap;
    -moz-justify-content: space-between;
    -webkit-justify-content: space-between;
    -ms-justify-content: space-between;
    justify-content: space-between;
    -moz-align-items: stretch;
    -webkit-align-items: stretch;
    -ms-align-items: stretch;
    align-items: stretch;
}

    .flex.flex-3 .flex-item {
        -ms-flex: 0 1 auto;
        -ms-flex-preferred-size: 30%;
        -moz-flex-basis: 30%;
        -webkit-flex-basis: 30%;
        -ms-flex-basis: 30%;
        flex-basis: 30%;
    }

        .flex.flex-3 .flex-item.left {
            text-align: right;
        }

    @media screen and (max-width: 980px) {

        .flex {
            -ms-flex-wrap: wrap;
            -moz-flex-wrap: wrap;
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        }

            .flex.flex-3 .flex-item {
                -ms-flex-preferred-size: 100%;
                -moz-flex-basis: 100%;
                -webkit-flex-basis: 100%;
                -ms-flex-basis: 100%;
                flex-basis: 100%;
            }

                .flex.flex-3 .flex-item.left {
                    text-align: right;
                }

    }

/* Wrapper */

.wrapper {
    padding: 6em 0 4em 0;
}

    .wrapper h1, .wrapper h2, .wrapper h3, .wrapper h4, .wrapper h5 {
        color: #004e64;
    }

    .wrapper.style1 {
        background: #004e64;
        color: #fcdcd6;
    }

        .wrapper.style1 h1, .wrapper.style1 h2, .wrapper.style1 h3, .wrapper.style1 h4, .wrapper.style1 h5 {
            color: #fff;
        }

        .wrapper.style1 cite.author {
            color: #fff;
        }

    @media screen and (max-width: 980px) {

        .wrapper {
            padding: 3em 0 1em 0;
        }

    }

    @media screen and (max-width: 480px) {

        .wrapper {
            padding: 2em 0 0.1em 0;
        }

    }

/* Header */

#header {
    -moz-align-items: center;
    -webkit-align-items: center;
    -ms-align-items: center;
    align-items: center;
    background: #fff;
    cursor: default;
    height: 6em;
    left: 0;
    line-height: 6em;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10001;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.075);
    text-align: center;
}

    #header .logo {
                    margin-top: 5px;
        text-decoration: none;
        display: inline-block;
    }

    #header nav {
        position: absolute;
        top: 0;
        height: inherit;
        line-height: inherit;
    }

        #header nav.left {
            left: 2em;
        }

        #header nav.right {
            right: 2em;
        }

        #header nav .button {
            padding: 0 2em;
            height: 3.25em;
            line-height: 3.25em;
        }

        #header nav a {
            text-decoration: none;
            display: inline-block;
        }

            #header nav a[href="#menu"] {
                text-decoration: none;
                -webkit-tap-highlight-color: transparent;
                font-size: 2em;
                color: #dee1e3;
                z-index: 10005;
            }

                #header nav a[href="#menu"]:before {
                    content: "";
                    -moz-osx-font-smoothing: grayscale;
                    -webkit-font-smoothing: antialiased;
                    font-family: FontAwesome;
                    font-style: normal;
                    font-weight: normal;
                    text-transform: none !important;
                }

                #header nav a[href="#menu"] span {
                    display: none;
                }

                #header nav a[href="#menu"]:before {
                    margin: 0 0.5em 0 0;
                }

@media screen and (max-width: 980px) {

    #header {
        height: 44px;
        line-height: 44px;
    }

        #header .logo {
            font-size: 1.25em;
            text-align: center;
        }

        #header nav a[href="#menu"] {
            font-size: 1.5em;
        }

        #header nav.left {
            left: 1em;
        }

        #header nav.right {
            display: none;
        }

}

@media screen and (max-width: 480px) {

    #header {
        min-width: 320px;
    }

}

/* Menu */

#menu {
    -moz-transform: translateX(-20em);
    -webkit-transform: translateX(-20em);
    -ms-transform: translateX(-20em);
    transform: translateX(-20em);
    -moz-transition: -moz-transform 0.5s ease, visibility 0.5s;
    -webkit-transition: -webkit-transform 0.5s ease, visibility 0.5s;
    -ms-transition: -ms-transform 0.5s ease, visibility 0.5s;
    transition: transform 0.5s ease, visibility 0.5s;
    -webkit-overflow-scrolling: touch;
    background: #25a2c3;
    color: #ffffff;
    height: 100%;
    max-width: 80%;
    overflow-y: auto;
    padding: 3em 2em;
    position: fixed;
    left: 0;
    top: 0;
    visibility: hidden;
    width: 20em;
    z-index: 10002;
}

    #menu > ul {
        margin: 0 0 1em 0;
    }

        #menu > ul.links {
            list-style: none;
            padding: 0;
        }

            #menu > ul.links > li {
                padding: 0;
            }

                #menu > ul.links > li > a:not(.button) {
                    border: 0;
                    border-top: solid 1px rgba(255, 255, 255, 0.25);
                    color: inherit;
                    display: block;
                    line-height: 3.5em;
                    text-decoration: none;
                }

                #menu > ul.links > li > .button {
                    display: block;
                    margin: 0.5em 0 0 0;
                }

                #menu > ul.links > li:first-child > a:not(.button) {
                    border-top: 0 !important;
                }

        #menu > ul.actions {
            display: none;
        }

    #menu .close {
        text-decoration: none;
        -moz-transition: color 0.2s ease-in-out;
        -webkit-transition: color 0.2s ease-in-out;
        -ms-transition: color 0.2s ease-in-out;
        transition: color 0.2s ease-in-out;
        -webkit-tap-highlight-color: transparent;
        border: 0;
        color: #a7d9e7;
        cursor: pointer;
        display: block;
        height: 3.25em;
        line-height: 3.25em;
        padding-right: 1.25em;
        position: absolute;
        right: 0;
        text-align: right;
        top: 0;
        vertical-align: middle;
        width: 7em;
    }

        #menu .close:before {
            content: "";
            -moz-osx-font-smoothing: grayscale;
            -webkit-font-smoothing: antialiased;
            font-family: FontAwesome;
            font-style: normal;
            font-weight: normal;
            text-transform: none !important;
        }

        #menu .close:before {
            font-size: 1.25em;
        }

        #menu .close:hover {
            color: #ffffff;
        }

        @media screen and (max-width: 736px) {

            #menu .close {
                height: 4em;
                line-height: 4em;
            }

        }

    #menu.visible {
        -moz-transform: translateX(0);
        -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
        transform: translateX(0);
        visibility: visible;
    }

    @media screen and (max-width: 980px) {

        #menu ul.actions {
            display: block;
        }

    }

    @media screen and (max-width: 736px) {

        #menu {
            padding: 2.5em 1.75em;
        }

    }

/* Banner */

#banner {
            position: relative;
    padding: 14em 0 12em 0;
    text-align: center;
}
    #banner::before {
            background-image: url("images/background.jpg");
    background-size: cover;
    background-position: center top;
    background-repeat: no-repeat;
            content: " ";
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: -2;
            opacity: 0.4;
    }
    #banner .content {
        -moz-transition: opacity 1s ease-in-out, -moz-transform 0.75s ease-in-out;
        -webkit-transition: opacity 1s ease-in-out, -webkit-transform 0.75s ease-in-out;
        -ms-transition: opacity 1s ease-in-out, -ms-transform 0.75s ease-in-out;
        transition: opacity 1s ease-in-out, transform 0.75s ease-in-out;
        opacity: 1;
                    animation: fadein 2s;
                    -moz-animation: fadein 2s; /* Firefox */
                    -webkit-animation: fadein 2s; /* Safari and Chrome */
                    -o-animation: fadein 2s;
    }@keyframes fadein {
from {
    opacity:0;
}
to {
    opacity:1;
}
}
@-moz-keyframes fadein { /* Firefox */
from {
    opacity:0;
}
to {
    opacity:1;
}
}
@-webkit-keyframes fadein { /* Safari and Chrome */
from {
    opacity:0;
}
to {
    opacity:1;
}
}
@-o-keyframes fadein { /* Opera */
from {
    opacity:0;
}
to {
    opacity: 1;
}
}

    #banner h1 {
        font-size: 3em;
        color: #264418;
        line-height: 1em;
        margin: 0 0 0.5em 0;
        padding: 0;
    }

    #banner p {
                    color: #555;
                    box-shadow: 0 0 20px rgba(255,255,255,.6);
                    background: rgba(255,255,255,.6);
        font-size: 1.25em;
        margin-bottom: 1.75em;
    }

    @media screen and (max-width: 1280px) {

