                font-size: 2.25em;
            }

    }

    @media screen and (max-width: 736px) {

        #banner {
            padding: 6em 4em 4em 4em;
        }

    }

    @media screen and (max-width: 480px) {

        #banner {
            padding: 4em 2em 2em 2em;
        }

